import React, {Component} from 'react';
import {Card,CardBody, CardImg, CardTitle,CardText} from 'reactstrap';
import dateFormat from 'dateformat'

class StaffDetail extends Component{
    render(){
        const staff1=this.props.staff
        return(
            <Card className='mb-5 bg-info text-white'>
                <div className='container row' key={staff1.id}>
                    <div className='left'>
                        <CardImg src={staff1.image}/>
                    </div>
                    <div className='right'>
                        <CardBody>
                            <CardTitle>H v tn: {staff1.name}</CardTitle>
                            <CardText>Ngy sinh: {dateFormat(staff1.doB,'dd/mm/yyyy')}</CardText>
                            <CardText>Phng ban:{staff1.department.name}</CardText>
                            <CardText>Bc lng: {staff1.salaryScale}</CardText>
                            <CardText>S ngy ngh cn li: {staff1.annualLeave}</CardText>
                            <CardText>S ngy  lm thm:{staff1.overTime}</CardText>
                        </CardBody>
                    </div>

                </div>
            </Card>
        );

    }
}
export default StaffDetail;


